import React, { useState } from 'react'
import * as styles from '/src/styles/embedyoutube.module.scss'

const EmbedYoutube = ({id}) =>{ //ID

    const [isThumbnail, setIsThumbnail] = useState(true); //useStateisThumbnail

    return (
        <div className={styles.youtubewrapper}>
            {isThumbnail ? ( //isThumbnailtrue
                <img className={styles.youtube}
                    src={`https://img.youtube.com/vi/${id}/maxresdefault.jpg`} //
                    onClick={() => setIsThumbnail(false)} //setIsThumbnailisThumbnailfalse
                    alt=""
                />
            ) : ( //isThumbnailfalse=iframeYoutube
            <iframe className={styles.youtube}
                src={`https://www.youtube.com/embed/${id}?autoplay=1`} //IDYoutube
                title="YouTube"
                frameBorder="0"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                allowFullScreen
            ></iframe>
            )}
        </div>
    );
}

export default EmbedYoutube